/* eslint-disable react/prop-types */

import React, { useState } from 'react';
import Col from '../../col';
import Grid from '../../grid';
import Icon from '../../icon';
import Radio from '../../radio';
import RadioErrorMessage from '../../radio-error-message';
import RadioGroup from '../../radio-group';
import RadioGroupControl from '../../radio-group-control';
import RadioGroupLegend from '../../radio-group-legend';
import RadioList from '../../radio-list';
import RadioListItem from '../../radio-list-item';
import Row from '../../row';
import Typography from '../../typography';

const RadioExample = () => {
  const [stateOne, setStateOne] = useState({
    selectedValue: '1'
  });

  const [stateTwo, setStateTwo] = useState({
    pizzas: [
      {
        checked: false,
        disabled: false,
        id: 1,
        label: 'Regular',
        value: 'regular'
      },
      {
        checked: false,
        disabled: false,
        id: 2,
        label: 'Deep Dish',
        value: 'deep-dish'
      },
      {
        checked: false,
        disabled: false,
        id: 3,
        label: 'Thin Crust',
        value: 'thin-crust'
      },
      {
        checked: false,
        disabled: true,
        id: 4,
        label: 'Super Thin Crust',
        value: 'super-thin-crust'
      }
    ],
    selectedPizza: 'regular'
  });

  const [stateThree, setStateThree] = useState({
    pizzas: [
      {
        checked: false,
        disabled: false,
        id: 1,
        label: 'Regular',
        value: 'regular'
      },
      {
        checked: false,
        disabled: false,
        id: 2,
        label: 'Deep Dish',
        value: 'deep-dish'
      },
      {
        checked: false,
        disabled: false,
        id: 3,
        label: 'Thin Crust',
        value: 'thin-crust'
      },
      {
        checked: false,
        disabled: true,
        id: 4,
        label: 'Super Thin Crust',
        value: 'super-thin-crust'
      }
    ],
    selectedPizza: 'regular'
  });

  const [stateFour, setStateFour] = useState({
    selected: ''
  });

  const handleChangeOne = e => setStateOne({ selectedValue: e.target.value });
  const handleChangeFour = e => setStateFour({ selected: e.target.value });

  const getChild = ({
    child,
    disabled,
    index,
    invalid,
    onChange,
    value
  }) => (
    React.cloneElement(child, {
      checked: child.props.value === value,
      disabled: child.props.disabled || disabled,
      invalid: child.props.invalid || invalid,
      key: index,
      onChange
    })
  );

  const CustomRadioGroup = ({
    children,
    disabled,
    errorMessage,
    invalid,
    isVertical,
    legend,
    onChange,
    value
  }) => (
    <RadioGroup disabled={disabled} invalid={invalid} onChange={onChange}>
      { legend ? <RadioGroupLegend>{legend}</RadioGroupLegend> : undefined }
      {
        isVertical
          ? <RadioList>
            {
              React.Children.map(children, (child, index) => (
                <RadioListItem key={index}>
                  {
                    getChild({
                      child,
                      disabled,
                      index,
                      invalid,
                      value
                    })
                  }
                </RadioListItem>
              ))
            }
          </RadioList>
          : <React.Fragment>
            {
              React.Children.map(children, (child, index) => getChild({
                child,
                disabled,
                index,
                invalid,
                value
              }))
            }
          </React.Fragment>
      }
      {
        invalid && errorMessage
          ? <RadioErrorMessage><Icon name="error" resolution="low" />{errorMessage}</RadioErrorMessage>
          : undefined
      }
    </RadioGroup>
  );

  return (
    <Grid>
      <Row>
        <Col>
          <Typography tag="h2">Radio default</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Radio
            name="radio-default-example"
            value="1"
            label="Radio One"
          />
          <Radio
            name="radio-default-example"
            value="2"
            label="Radio Two"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Radio (stateful) with preselection.</Typography>

          <Typography>Composition approach</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Radio
            defaultChecked
            name="radio-group-stateful-example"
            value="1"
            label="Radio One"
          />
          <Radio
            name="radio-group-stateful-example"
            value="2"
            label="Radio Two"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Radio (stateless) with preselection.</Typography>

          <Typography>Composition approach</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <RadioGroup>
            <Radio
              checked={stateOne.selectedValue === '1'}
              label="Radio One"
              name="radio-group-stateless-preselection-example"
              onChange={handleChangeOne}
              value="1"
            />
            <Radio
              checked={stateOne.selectedValue === '2'}
              label="Radio Two"
              name="radio-group-stateless-preselection-example"
              onChange={handleChangeOne}
              value="2"
            />
          </RadioGroup>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Radio Create your own wrapper example</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <CustomRadioGroup
            value={stateTwo.selectedPizza}
            onChange={e => setStateTwo({ ...stateTwo, selectedPizza: e.target.value })}
          >
            {
              stateTwo.pizzas.map((pizza, k) => (
                <Radio
                  disabled={pizza.disabled}
                  key={`custom-radio-group-pizza-${k}`}
                  id={`custom-radio-group-pizza-${k}`}
                  label={pizza.label}
                  name="custom-pizza-radios"
                  value={pizza.value}
                />
              ))
            }
          </CustomRadioGroup>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Radio Group Control (legacy wrapper)</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <RadioGroupControl
            value={stateThree.selectedPizza}
            onChange={e => setStateThree({ ...stateThree, selectedPizza: e.target.value })}
          >
            {
              stateThree.pizzas.map((pizza, k) => (
                <Radio
                  disabled={pizza.disabled}
                  key={`custom-radio-group-pizza-${k}`}
                  id={`legacy-radio-group-control-pizza-${k}`}
                  label={pizza.label}
                  name="legacy-radio-control-pizza-radios"
                  value={pizza.value}
                />
              ))
            }
          </RadioGroupControl>

        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Radio Group with validation</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <RadioGroup invalid={!stateFour.selected}>
            <RadioGroupLegend>Select Pizza Crust</RadioGroupLegend>
            <RadioList>
              <RadioListItem>
                <Radio
                  invalid={!stateFour.selected}
                  label="Regular"
                  name="radio-group-validation-example"
                  onChange={handleChangeFour}
                  value="Regular"
                />
              </RadioListItem>
              <RadioListItem>
                <Radio
                  invalid={!stateFour.selected}
                  label="Deep Dish"
                  name="radio-group-validation-example"
                  onChange={handleChangeFour}
                  value="Deep Dish"
                />
              </RadioListItem>
              <RadioListItem>
                <Radio
                  invalid={!stateFour.selected}
                  label="Thin Crust"
                  name="radio-group-validation-example"
                  onChange={handleChangeFour}
                  value="Thin Crust"
                />
              </RadioListItem>
            </RadioList>
            {
              stateFour.selected === ''
                ? <RadioErrorMessage><Icon name="error" resolution="low" /> Please select crust type.</RadioErrorMessage>
                : undefined
            }
          </RadioGroup>
        </Col>
      </Row>
    </Grid>
  );
};

export default RadioExample;
